.home{
    h2, p{
        margin-bottom: 15px;  
    }
    p{
        font-size: 1.1em;
    }
}
.home-transforming{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 150px;  
}

.vesta-transforming-workplace{ 
    display: flex;
    flex-direction: column;
    width: 100%;
    // min-width: 420px;
    padding-right: 100px;
    
    img{
        height:auto;
        width: 80%;
        min-width: 375px;
        margin-bottom: 30px;     
    }
    h2{
        margin: 35px 0 15px 0;  
    }


}

.lady-wrapper{
    width: 100%;
    height: auto;

    img{
        width: 100%;
        max-width: 415px;
        min-width: 312px;
    }
}

.home-the-problem{
    width: 100%;
    display: flex;
    margin-top: 90px;
    margin-bottom: 150px;
}
.problem-article{
    width: 50%;
    max-height: 160px;
    p{
        padding-right: 60px;
        max-width: 450px;
    }
}
.problem-widget{
    width: 50%;
    img{
        width: 100%;
        height: auto;
        min-width: 350px;
    }
}
    
.home-strength{
    margin-top: 90px;
    
}
.strength-numbers{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin-bottom: 100px;
    flex:100%;
    .strength-number-80,.strength-number-85, .strength-number-95{
        width: 30%;
        margin-top: 50px;
        img{
            width: 45%;
            height:auto;
            max-width: 100%;
            display: block;
            margin: 0 auto;
        }
    }
    hr{
        width: 155px;
        border: solid 0.5px $font-color;
        margin: 20px auto 5px auto;  
    }
    p{
        text-align: center;
        max-width: 250px;
        margin: 0 auto;
    }
}
.home-solution{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    margin: 0 0 150px -16.5%;
 
    .inner-solution{
        min-width: 350px;
        margin-left: 40px;

        button{
            @include book-btn;
            font-size: 0.95rem;
            padding: 15px 18px;
            margin-top: 10px;
        }
        p{
            font-size: 1em;
        }
    }
}
.solution-image-container{
    .solution-image{
        width: 100%;
        height: auto;
        min-width: 675px;
        min-height: 375px;
    }

}
.home-customer-stories{
    margin-bottom: 150px;
    .header-style-container{
        margin-bottom: 80px;
    }
}
.trusted-partners{
    margin: 50px 0 150px 0;
    .header-style-container{
        margin-bottom: 50px;
    }
    
}
.partners-list{
    width: 100%;
    display: flex;
    flex-direction: row;

    .govt-canada{
        width: 100%;
        height:auto;
    }
    .legal-innovation{
        width: 90%;
        height:auto;
        margin-top: 30px;
    }
    .social-venture{
        width: 90%;
        height:auto;
        margin-top: 40px;
    }
    .unilever{
        width: 90%;
        height:auto;
    }
}
.home-subscribe{
    margin: 50px 0 0 0;
    h2{
        font-size: 28px;
        text-align: center;
        margin-top: 100px;
    }

}

.subscribe-form-container{
    width: 100%;
    margin-bottom: 131px;
}

.subscribe-form{
    width: 75%;
    max-width: 560px;
    text-align: left;
    margin: 30px auto 0 auto;
    h3{
        font-size: 1.1em;
        font-family: 'Century Gothic Bold';
        color: $nav-color; 
        text-align: left;
    }
}
.subscribe-email{
    height:47px;
    min-width: 400px;
    margin-top: 20px;
    font-size: 14px;
    font-family: 'Century Gothic';
    padding-left:20px;
    color: $font-color;
    border: 1px solid $font-color;
}
.subscribe-button{
    margin: 20px 0 0 -10px;
    padding: 13px 15px 16px 15px;
    width: 25%;
    max-width: 175px;
    background-color: $accent-button-color;
    border: none;
    color: white;
    font-size: 1em;
    font-family: 'Century Gothic';
    text-transform: uppercase;

}
